import { Map, MapTypeControl, ZoomControl, Circle } from "react-kakao-maps-sdk";
import useMainStore from "../store/useMainStore";
import { fetchCoordinate, fetchKeyword } from '../api/fetchPlaces'
import { useRef, useEffect, useState } from "react";
import { useQuery } from "@tanstack/react-query";
import { Link } from "react-router-dom";
import {useGeolocation} from "../hook/useGeoLocation";


const KakaoMap = () => {
    /* eslint-disable */
    const {
      searchList,         //  store
      map,                //    store
      setMap,
      setMarkers,
      center,             //    store
      setCenter,
      level,              //   store
      setLevel,
      coordinates,        //   store
      setCoordinates,
      keywordCoordinates, //   store
      setKeywordCoordinates,
      // 2024.10.16      
      currentLocation,
      setCurrentLocation
    } = useMainStore()

    //   api query
    const { data, refetch } = useQuery({
        queryKey: ['coordinateAddress', coordinates],
        queryFn: () => fetchCoordinate(coordinates), 
        enabled: coordinates.x !== null && coordinates.y !== null, 
    });

    //    api query
    const { data: keywordData, refetch: keywordRefetch } = useQuery({
        queryKey: ['fetchKeyword', keywordCoordinates, coordinates.x, coordinates.y],
        queryFn: () => fetchKeyword({
            term: keywordCoordinates,
            x: coordinates.x,
            y: coordinates.y,
        }),
        enabled: keywordCoordinates !== null && coordinates.x !== null && coordinates.y !== null,
    });
    //   
    useEffect(() => {
        if (searchList && searchList.length > 0) {
            const bounds = new kakao.maps.LatLngBounds();
            const markers = [];
            //       
            searchList.forEach((item) => {
                const boundPosition = new kakao.maps.LatLng(searchList[0].y, searchList[0].x); //   (, )
                const markerPosition = new kakao.maps.LatLng(item.y, item.x); //   (, )
                const marker = new kakao.maps.Marker({
                    position: markerPosition,
                });
                setCenter({
                    lat: searchList[0].y,
                    lng: searchList[0].x,
                })
                setLevel(2);
                marker.setMap(map); //   
                markers.push(marker); //   
                bounds.extend(boundPosition); //     
            });
            setMarkers(markers); // Zustand    
            map.setBounds(bounds); //        
        }
    }, [searchList])
    
    
    //   useQuery    data 
    //     customOverlay 
    const customOverlayRef = useRef(null);

    const handleMapClick = (mapData, event) => {
        const x = event.latLng.getLng();
        const y = event.latLng.getLat();
        setCoordinates({ x, y });
        //     
        if (customOverlayRef.current) {
            customOverlayRef.current.setMap(null); //   
            customOverlayRef.current = null; //   null 
        }
    };


    useEffect(() => {
        if(coordinates.x !== null && coordinates.y !== null) {
            refetch();
        }
    }, [coordinates, refetch]);


    useEffect(() => {
        if(data) {
            if (data.length > 0) {
                const addressName = data[0]?.address?.address_name; 
                if (addressName) {
                    setKeywordCoordinates(addressName);
                    keywordRefetch();
                }
            }
        }
    }, [data, keywordRefetch]);
    useEffect(() => {
        if (keywordData && keywordData.length > 0) {
            const x = coordinates.x;
            const y = coordinates.y;
            const content = `
                <div class="info_wrap">
                    <div class="info_title">
                        ${keywordData[0].place_name}
                    </div>
                    <div class="btn_wrap">
                        <a target="_blank" href="${keywordData[0].place_url}">
                             
                        </a>
                    </div>
                </div>
            `;
            customOverlayRef.current = new window.kakao.maps.CustomOverlay({
                position: new window.kakao.maps.LatLng(y, x),
                content: content,
                clickable: true,
                zIndex: 1
            });
            customOverlayRef.current.setMap(map);
        }

    }, [keywordData, coordinates, map]);


    // 2024.10.16    _   
    const { latitude, longitude, accuracy, error } = useGeolocation();
    const [ location, setLocation ] = useState(false)

    const handleLocation = () => {
        setLocation(true)
        if(location) {
            setCenter({
                lat: latitude,
                lng: longitude,
            })
        }
    }
    return (
        <div className="kakao_map_wrapper">
            <Map //   Container
                center={{
                    lat: center.lat,
                    lng: center.lng,
                }}
                level={level}
                onCreate={setMap}
                className="kakao_map"
                onClick={(data, event) => handleMapClick(data, event)}
            >
                <MapTypeControl position={"TOPRIGHT"} />
                <ZoomControl position={"RIGHT"} />
                {/*       */}
                {
                    location &&
                        <Circle
                            center={{
                            lat: latitude,
                            lng: longitude,
                            }}
                            radius={5}
                            strokeWeight={15} //  
                            strokeColor={"#ff3e3f"} //  
                            strokeOpacity={.2} //    1 0   0  
                            strokeStyle={"solid"} //   
                            fillColor={"#ff3e3f"} //  
                            fillOpacity={1} //   
                        />
                }
                <button className="location_btn" onClick={handleLocation}></button>
            </Map>
        </div>
    )
}

export default KakaoMap